<template>
  <v-container fluid>
    <v-layout class="browser-height">
      <v-flex
        sm6
        xs12
        md6
        lg8
        class="scroll"
      >
        <material-card
          flat
          full-width
          paging-btn
          :prev-btn="prevBtn"
          :next-btn="nextBtn"
          @update="updateData"
        >
          <div
            v-if="loading"
            class="loading"
          >
            Loading...
          </div>
          <div v-else>
            <Res-list
              link-btn
              :items="restaurantList"
              @enableShowInfoWindow="enableShowInfoWindow"
              @disableShowInfoWindow="disableShowInfoWindow"
            />    <!-- Add restaurant table.vue  -->
          </div>
        </material-card>
      </v-flex>
      <div class="custom-width">
        <google-map
          v-if="!loading"
          :info-window-index="infoWindowIndex"
          :restaurant-info="restaurantList"
        />  <!-- Add Map.vue -->
      </div>
    </v-layout>
  </v-container>
</template>

<script>
import GoogleMap from '@/components/local/GoogleMap.vue'
import ResList from '@/components/local/list/RestaurantList.vue'
import { restful } from '../api'
import { urls } from '../api/requestUrl.js'

export default {
  components: {
    'google-map': GoogleMap,
    'Res-list': ResList
  },
  props: {
    keyword: {
      type: Array,
      default: null
    }
  },
  data () {
    return {
      restaurantList: [],
      resListLinks: [],
      loading: false,
      status: false,
      infoWindowIndex: null,
      prevBtn: false,
      nextBtn: false
    }
  },
  watch: {
    resListLinks (val) {
      this.prevBtn = false
      this.nextBtn = false
      for (let i = 0; i < this.resListLinks.length; ++i) {
        if (this.resListLinks[i].rel === 'prev') {
          this.prevBtn = true
        } else if (this.resListLinks[i].rel === 'next') {
          this.nextBtn = true
        }
      }
    }
  },
  created () {
    if (this.$route.query.restaurantName !== '' && this.$route.query.restaurantName !== undefined) {
      urls.restaurants.data.name = this.$route.query.restaurantName
    }
    this.restaurantFetchData(urls.DOMAIN + urls.restaurants.path, urls.restaurants.data)
  },
  methods: {
    restaurantFetchData (url, data) {
      this.loading = true
      restful.getRequest(urls.restaurants.method, url, data)
        .then(result => {
          console.log(result)
          this.restaurantList = result.data.response.content
          this.resListLinks = result.data.response.links
        })
        .catch(result => {
        })
        .finally(() => {
          urls.restaurants.data.name = null
          this.loading = false
        })
    },
    updateData (link) {
      for (let i = 0; i < this.resListLinks.length; ++i) {
        if (this.resListLinks[i].rel === link) {
          console.log(this.resListLinks[i].href + '&restaurantName=' + this.$route.query.restaurantName)
          this.restaurantFetchData(this.resListLinks[i].href + '&name=' + this.$route.query.restaurantName)
          break
        }
      }
    },
    enableShowInfoWindow (restaurant) {
      this.infoWindowIndex = restaurant
    },
    disableShowInfoWindow (restaurant) {
      this.infoWindowIndex = restaurant
    }
  }
}
</script>

<style>
.mapouter {
  text-align:right;
  height:95%;
  width:100%;
  position: absolute;
}
.gmap_canvas {
  overflow:hidden;
  background:none!important;
  height:100%;
  width:100%;
}
.browser-height {
  height: 90vh;
}
.scroll {
  overflow:scroll;
}
.text-center {
  text-align: center!important;
}
.custom-width {
    width: 100%;
}
</style>
